const goLogin = () => {
	
	let validation = new Promise(function(resolve , reject){
		var _self = this
		try { //
			var load = uni.getStorageSync('isCanUse')
			if (load == true) {
				// console.log('')
				resolve(load)
			} else {
				uni.navigateTo({
					url: "../authorization/authorization"
				})
			}
		} catch (e) {}
	})
	return validation
	  
}

export default {
	goLogin
}
