import url from "../static/url.js"
let token = ""

const baseUrl = url.appUrl; //,
// ()
const baseRequest = (opts, data) => {
	try { //token
		token = uni.getStorageSync('token');   //token
	} catch (e) {}
	let baseDefaultOpts = {
		url: baseUrl + opts.url,
		// 
		data: data,
		// 
		method: opts.method,
		// 
		header:{
			"Authorization":token,
		},
		// 
		dataType: 'json',
	}
	let promise = new Promise(function(resolve, reject) {
		uni.request(baseDefaultOpts).then(
			(res) => {
				// console.log('request', res)
				resolve(res[1]) //
				// if(res[1].data.state == '100' || res[1].data.state == 100){
				// 	// 100,,appconsole.log(JSON.stringify(res[1].data))
				// 	resolve(res[1].data)
				// }
				// if(res[1].data.state == '105' || res[1].data.state == 105){
				// 	// 105
				// 	uni.showToast({
				// 		icon:'none',
				// 	    title: '',
				// 	    duration: 2000 
				// 	}); 
				// 	// 
				// 	return false
				// }
			}
		).catch(
			(response) => {
				reject(response)
			}
		)
	})
	return promise
};

export default {
	baseUrl,
	baseRequest,
}
